/*tab-bar*/
.list-group {
    flex-direction: row;
    span {
        vertical-align:super;
        font-size: 12px;
    }
}

/*product image*/
.proimg-size1 {
    height: 240px;
}
.proimg-size2 {
    height: 480px;
}
.tab-content {
    .card {
        margin-top: 48px;
    }
    @media (max-width: 767px) {  /*mobile*/
        .card-columns {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
        }
        .card img{
            width: 161px;
            height: 161px;
        }
    }
}